// custom style variables



$ci-color-1: #FFE7B7 !default; // logo yellow
$ci-color-2: #4E3B2B !default; // dark logo bg

$ci-border-color: #E0E0E0 !default;
$ci-light-block-bg: #f8f8f8 !default;

$primary: $ci-color-1 !default;
$secondary: $ci-color-2 !default;
$light: $ci-light-block-bg !default;
$white: #ffffff !default;
$dark: $ci-color-2 !default;
$darker: darken($ci-color-2, 7.5) !default;

$warning: #FFD768 !default;
$danger: #FF3803 !default;

$text-muted: #999999 !default;

$border-color: $ci-border-color !default;


$body-bg: $ci-color-2 !default;
$body-color: $white !default;
$headings-color: darken($white, 10) !default;


