// TEXT

$bsx-text-column-max-width:   672px !default;

$bsx-line-label-text-transform: uppercase !default;





// NAV

$bsx-appnav-navbar-bg-color:				rgba($darker, 0.94) !default; // header

$bsx-appnav-navbar-brand-max-width:			150px !default;

$bsx-appnav-quickstart-bg-color: 			$darker !default;

$bsx-appnav-quickstart-color: 				$white !default;
$bsx-appnav-quickstart-inverse-color: 		$white !default;
$bsx-appnav-quickstart-highlight-color: 	$white !default;

$bsx-appnav-quickstart-bg-color: 			$dark !default;
$bsx-appnav-quickstart-dropdown-bg-color: 	$dark !default;
$bsx-appnav-mobile-collapse-bg-color:		$bsx-appnav-quickstart-dropdown-bg-color !default; // mobile nav background

$bsx-appnav-nav-desktop-level-1-hover-link-bg-color:        $dark !default; // desktop level 1 hover bg color (equal desktop dropdown bg 

$bsx-appnav-navbar-box-shadow:				0 1px 0 0 rgba(255, 255, 255, 0.1), 0 0 15px 0 rgba(255, 255, 255, 0.05) !default;

$bsx-appnav-dropdown-box-shadow:			0 0 0.125rem 0 rgba(255, 255, 255, 0.25) !default;

$bsx-appnav-quickstart-mobile-link-border-color: 	rgba($bsx-appnav-quickstart-color, 0.075) !default;

